import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-photo-tab',
  templateUrl: './photo-tab.component.html',
  styleUrls: ['./photo-tab.component.css']
})
export class PhotoTabComponent implements OnInit {

  constructor() { }

  ngOnInit() {
    this.getPhotos();
  }

  photoUrl1;
  photoUrl2;
  photoUrl3;
  photoUrl4;



  @Input()
  placePhoto;

  photoUrl;
  getPhotos(){
    if(typeof(this.placePhoto)=="undefined")
      return;
    console.dir(this.placePhoto);
    // console.log("Enter 1");
    this.photoUrl=[];

    this.photoUrl1=[];
    this.photoUrl2=[];
    this.photoUrl3=[];
    this.photoUrl4=[];

    for(var i=0;i<this.placePhoto.length;i++){
      var url=this.placePhoto[i].getUrl({'maxWidth':this.placePhoto[i].width});
      this.photoUrl.push(url);

      if(i%4==0){
        this.photoUrl1.push(url);
      }
      if(i%4==1){
        this.photoUrl2.push(url);
      }
      if(i%4==2){
        this.photoUrl3.push(url);
      }
      if(i%4==3){
        this.photoUrl4.push(url);
      }
    }
  }

  hasPhoto(){
    if(this.photoUrl.length>0)
      return true;
    else
      return false;
  }
}



