import { Component, ViewChild } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import {PlacListComponent}from './plac-list/plac-list.component';

import {DataService} from './data.service';
import {trigger,state, style, transition, animate, keyframes,query,stagger} from '@angular/animations';


declare var google; 

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  animations:[
    trigger('move', [
      // state('placList', style({ offset:0 })),
      // state('placDetail', style({ offset:0 })),

      transition('placList=>placDetail', animate(1000, keyframes([
        style({  transform:"translateX(-75%)" }), 
        style({  transform:"translateX(0)" }) 
      ]))),
      transition('placDetail=>placList', animate(1000, keyframes([
        style({  transform:"translateX(75%)" }), 
        style({  transform:"translateX(0)" }) 
      ]))),
      transition('favList=>placDetail', animate(1000, keyframes([
        style({  transform:"translateX(-75%)" }), 
        style({  transform:"translateX(0)" }) 
      ]))),
      transition('placDetail=>favList', animate(1000, keyframes([
        style({  transform:"translateX(75%)" }), 
        style({  transform:"translateX(0)" }) 
      ])))
    ])
  ]
})


export class AppComponent {
  title = 'app';

  //determine show which tab
  ResTab:String="";


  testStr:String="HaHa";
  errorStr:String="";
  api_error:String="";
  isReqing:boolean=false;

  constructor(private http:HttpClient,private _data:DataService) { }

  ////////////////////////////////////////////////////////
  //  place list operation
  ////////////////////////////////////////////////////////
  formData;
  placeList;

  @ViewChild('child_placlist')
  private placlistC:PlacListComponent;

  submitForm($event) {
    if(typeof($event.keyword)!="undefined")
      this.formData=JSON.parse(JSON.stringify($event));

    console.dir(JSON.stringify(this.formData));
    this.isReqing=true;
    this.errorStr=undefined;
    this._data.place_id=undefined;
    this.cur_page_no=0;
    document.getElementById("ResLink").click();
    // this.placlistC.clearPage();

    if(this.placlistC)
      this.clearAll();

    this.http.post(this._data.hostURL+'/api/placelist',$event).subscribe(
      data=>{
        console.dir(data);
        //cannot get result
        if(typeof(data['Error'])!="undefined"){
          this.errorStr=data['Error'];
        }
        else{
          this.placeList=data;          
        }
      
      },
      err=>{
        console.error(err);
        this.api_error="Error";
        this.isReqing=false;                
      },
      ()=>{
        // console.dir(JSON.stringify(this.placeList));
        if(this.placeList.curlat){
          this._data.curLat=this.placeList.curlat;
          this._data.curLng=this.placeList.curlng;
        }
        else{
          this._data.curLat=this.formData.lat;
          this._data.curLng=this.formData.lng;
          
        }
        this.isReqing=false;
        this.ResTab="placList";

        this.api_error=undefined;        
      }
    )

  }    

  ////////////////////////////////////////////////////////
  //  page operation
  ////////////////////////////////////////////////////////
  cur_page_no:number=0;

  pageInfo;
  retrievePage($event){
    this.pageInfo=$event;

    console.log("pageInfo: "+this.pageInfo.page_no);
    console.log("curPage: "+this.cur_page_no);


    //get origin page
    if(this.pageInfo.page_no==0){
      this.http.post(this._data.hostURL+'/api/placelist',this.formData).subscribe(
        data=>{this.placeList=data},
        err=>{
          console.error(err);
          this.api_error="Error";     

        },
        ()=>{
          // console.dir(JSON.stringify(this.placeList));
          console.log("Send zero page request");                 
          
          this.cur_page_no=0;
          this.placlistC.redPageNo();
          this.placlistC.isReqing=false;

          this.api_error=undefined;          
        }
      )
    }
    else{ 
      this.http.post(this._data.hostURL+'/api/nextpage',{page_token:this.pageInfo.page_token}).subscribe(
        data=>{this.placeList=data},
        err=>{
          console.error(err);
          this.api_error="Error";      
                     
        },
        ()=>{
          console.log("Send next page request");
          // console.dir(JSON.stringify(this.placeList));     

          if(this.pageInfo.page_no>this.cur_page_no){
            this.cur_page_no++;
            this.placlistC.addPageNo();
            this.placlistC.isReqing=false;            
          }
          else{
            this.cur_page_no--;
            this.placlistC.redPageNo();
            this.placlistC.isReqing=false;            
          }

          this.api_error=undefined;          
        }
      )
    }
    
  }


  ////////////////////////////////////////////////////////
  //  detail operation
  ////////////////////////////////////////////////////////
  placeDetail;
  detailService;
  placeMap;
  getDetail($event){
    if(typeof($event.place_id)=="undefined"){
      return;
    }

    this.placeMap = new google.maps.Map(document.getElementById('gmap'), {
      center: {lat: $event.lat, lng: $event.lng},
      zoom: 15
    });
    this.detailService=new google.maps.places.PlacesService(this.placeMap);
    this.detailService.getDetails({
      placeId: $event.place_id
    }, function(place, status) {
      if (status === google.maps.places.PlacesServiceStatus.OK) {
        console.dir(place);
 
        return place;
      }
    });

  }

  placeInfo;
  prevTab;
  turnToDetail($event){
    if(typeof($event.place_id)=="undefined"){
      return;
    }
    // console.dir($event);
    this.prevTab=this.ResTab;
    
    this.placeInfo=$event;
    // console.dir(JSON.stringify(this.placeInfo));
    this.ResTab="placDetail";
    this.detailAva=true;
  }


  returnList($event){
    this.ResTab=this.prevTab;
  }

  detailAva=false;
  enterDetail($event)
  {
    this.prevTab=$event;
    this.ResTab="placDetail";
  }


  ////////////////////////////////////////////////////////
  //  clear operation
  ////////////////////////////////////////////////////////
  clearOpr($event){
    
    this.clearAll();
  }

  clearAll(){
    this.isReqing=false;
    this.errorStr=undefined;
    this.api_error=undefined;
    this._data.place_id=undefined;
    this.cur_page_no=0;

    this.ResTab="placList";
    this.placlistC.clearPage();
    this.ResTab="";
    

    this.placeList=undefined;


    document.getElementById("ResLink").click();
  }
}



