import { Injectable } from '@angular/core';
import { BehaviorSubject} from 'rxjs/BehaviorSubject'


@Injectable()
export class DataService {


  constructor() { }

  curLat;
  curLng;

  setLatLng(lat,lng){
    this.curLat=lat;
    this.curLng=lng;
  }

  place_id;

  hostURL="http://web8-env.us-east-2.elasticbeanstalk.com";
  // hostURL="http://localhost:3000";
}



