import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';


import { AppComponent } from './app.component';

import { NgbModule } from "@ng-bootstrap/ng-bootstrap";
import { FormsModule } from '@angular/forms';
import { FormComponent } from './form/form.component';

import { HttpClientModule } from '@angular/common/http';  // replaces previous Http service

import { NgSwitch } from '@angular/common';
import { PlacListComponent } from './plac-list/plac-list.component';
import { PlacDetailComponent } from './plac-detail/plac-detail.component';
import { FavListComponent } from './fav-list/fav-list.component';
import {InfoTabComponent}from './info-tab/info-tab.component';
import {PhotoTabComponent}from './photo-tab/photo-tab.component';
import { MapTabComponent } from './map-tab/map-tab.component';


import {DataService} from './data.service';
import { ReviewsTabComponent } from './reviews-tab/reviews-tab.component';

import {BrowserAnimationsModule } from '@angular/platform-browser/animations';

@NgModule({
  declarations: [
    AppComponent,
    FormComponent,
    PlacListComponent,
    PlacDetailComponent,
    FavListComponent,
    InfoTabComponent,
    PhotoTabComponent,
    MapTabComponent,
    ReviewsTabComponent
  ],
  imports: [
    BrowserModule,
    NgbModule.forRoot(),
    FormsModule,
    HttpClientModule,
    BrowserAnimationsModule
  ],
  providers: [DataService],
  bootstrap: [AppComponent]
})
export class AppModule { }



