import { Component, OnInit ,Output, EventEmitter,NgZone} from '@angular/core';
import { HttpClient } from '@angular/common/http';


declare var google: any;

@Component({
  selector: 'app-form',
  templateUrl: './form.component.html',
  styleUrls: ['./form.component.css']
})
export class FormComponent implements OnInit {

  isCurLoc:boolean=true;
  
  Lat:number;
  Lng:number;

  isLoadCurLoc:boolean=false;

  constructor(private http:HttpClient,private _ngZone: NgZone) { }

  ngOnInit() {
    this.getCurLoc();

    this.initInputAC();
  }

  data={
    keyword:"",
    category:"default",
    distance:10,
    lat:0,
    lng:0,
    locName:"",
    isCurLoc:true
  }


  getCurLoc(){
    this.http.get('http://ip-api.com/json').subscribe(
      data=>{this.data.lat=data["lat"];this.data.lng=data["lon"];},
      err=>console.error(err),
      ()=>{this.isLoadCurLoc=true;}
    )
  }

  /////////////////////////////////////////////////////////////////////////
  //  auto complete
  /////////////////////////////////////////////////////////////////////////
  inputAC;
  initInputAC(){
    this.inputAC = new google.maps.places.Autocomplete(
      (document.getElementById('oth_loc_val')),
      {types: ['geocode']});

      // When the user selects an address from the dropdown, populate the address
      // fields in the form.
      this.inputAC.addListener('place_changed', function(){
        window['FormComponent'].zone.run(() => {
          window['FormComponent'].component.setOthLocInput();




    
        });
      });
  }

  setOthLocInput(){
    var place=this.inputAC.getPlace();
    this.data.locName=place.formatted_address;
  }



  //send submit event to parent component
  @Output()
  submit=new EventEmitter();

  onSubmit(value: any) {

    this.data.isCurLoc=this.isCurLoc;

    this.submit.emit(this.data);   

    // return false;
  }    

  //////////////////////////////////////////////////////////
  //  Error Operation
  //////////////////////////////////////////////////////////
  keywordInfo;
  kwErrStyle;
  keywordAva:boolean=false;
  checkKeyword(_valid){
    if(_valid){
      this.keywordInfo=undefined;
      this.kwErrStyle=undefined;
      this.keywordAva=true;
    }
    else{
      this.keywordInfo="Please enter a keyword.";
      this.kwErrStyle="red";
      this.keywordAva=false;      
    }
  }



  locNameInfo;
  lcErrStyle;
  locNameAva:boolean=false;
  checkLocName(_valid){
    if(_valid){
      this.locNameInfo=undefined;
      this.lcErrStyle=undefined;
      this.locNameAva=true;
    }
    else{
      this.locNameInfo="Please enter a location.";
      this.lcErrStyle="red";
      this.locNameAva=false;      
    }
  }



  @Output()
  resetReq=new EventEmitter();
  resetComp(){
    this.isCurLoc=true;
    this.data={
      keyword:"",
      category:"default",
      distance:10,
      lat:0,
      lng:0,
      locName:"",
      isCurLoc:true
    }
    // this.ngOnInit();
    this.getCurLoc();
    this.initInputAC();

    this.keywordInfo=undefined;
    this.kwErrStyle=undefined;
    this.keywordAva=false;

    this.locNameInfo=undefined;
    this.lcErrStyle=undefined;
    this.locNameAva=false;

    this.resetReq.emit("reset");
  }

}



