import { Component, OnInit, Input,Output,EventEmitter } from '@angular/core';

@Component({
  selector: 'app-info-tab',
  templateUrl: './info-tab.component.html',
  styleUrls: ['./info-tab.component.css']
})
export class InfoTabComponent implements OnInit {

  constructor() {}

  @Input() placeDetail;
  @Input() curDay;

  ngOnInit() {

  }


  getTodayOpenTime(){
    var week_day_time=this.placeDetail.opening_hours.weekday_text;

    // this.reorderWeekday();

    //start by Sunday
    if(week_day_time[0].charAt(0)=='S'){
      var tmp=week_day_time[this.curDay].split(': ');
      return tmp[1];
    }
    else{
      var tmp;
      if(this.curDay==0)
        tmp=week_day_time[6].split(': ');
      else
        tmp=week_day_time[this.curDay-1].split(': ');
      return tmp[1];
    }

  }


  reorderWeekdayText=[];
  reorderWeekday(){
    this.reorderWeekdayText.length=0;
    var week_day_time=this.placeDetail.opening_hours.weekday_text;

    //start by Sunday
    if(week_day_time[0].charAt(0)=='S'){
      for(var i=this.curDay;i<7;i++){
        this.reorderWeekdayText.push(week_day_time[i]);
      }

      for(i=0;i<this.curDay;i++){
        this.reorderWeekdayText.push(week_day_time[i]);
      }

    }
    //start by Monday
    else{
      var tmp_day;
      if(this.curDay==0)
        tmp_day=6;
      else
        tmp_day=this.curDay-1;

      for(i=tmp_day;i<7;i++){
        this.reorderWeekdayText.push(week_day_time[i]);
      }

      for(i=0;i<tmp_day;i++){
        this.reorderWeekdayText.push(week_day_time[i]);
      }
    }
  }

  getReorderWeekdayText(i){
    return this.reorderWeekdayText.slice(1);
  }

  //show symbol
  showPriceLevel(pl){
    if(pl==0)
      return 0;

    var res="";
    for(var i=0;i<pl;i++){
      res+='$';
    }
    return res;
  }


  getRatingRatio(ra){
    return (ra*1.0/5)*100+'%';
  }

}



